/* Conteneur principal */
.admin-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;   /* centre les sections horizontalement */
  font-family: Arial, sans-serif;
  justify-content: flex-start; /* place le contenu au début de la page */
  min-height: 100vh; /* prend toute la hauteur de la fenêtre */
  margin: 0;
  padding-top: 20px; /* ajoute un espace en haut */
}

/* Sections Admin et Teams */
.admin-section,
.teams-section {
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 5px;
  background-color: #fff;
  display: inline-flex;   /* prend la largeur du contenu */
  flex-direction: column;
  gap: 20px;
  max-width: fit-content; /* limite la largeur au contenu */
}

.admin-section h2,
.teams-section h2 {
  margin-top: 0;
}

/* Contenu des sections */
.admin-content,
.teams-content {
  display: flex;
  justify-content: flex-start; /* aligne les blocs à gauche */
  align-items: flex-start;
  gap: 20px; /* espace entre tableau et bloc add */
  width: auto;
}

/* Tables */
.admin-list table,
.teams-list table {
  width: auto;
  margin: 0; /* supprime le centrage horizontal */
  table-layout: auto;
  border-collapse: collapse;
  white-space: nowrap;
}

.admin-list th,
.admin-list td,
.teams-list th,
.teams-list td {
  border: 1px solid #ddd;
  padding: 8px 12px;
  text-align: left;
}

/* Common styles for add buttons */
.common-add button {
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.common-add button:hover {
  background-color: #0056b3;
}

/* Bloc ajout Admin et Teams */
.admin-add,
.teams-add {
  display: flex;
  flex-direction: row; /* place les champs et boutons côte à côte */
  align-items: center;
  gap: 10px;
}

.admin-add select,
.teams-add input {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.admin-add button,
.teams-add button {
  @extend .common-add button;
}

/* Managers dans Teams */
.manager-container {
  display: flex;
  align-items: center;
  justify-content: space-between; /* Align children to opposite sides */
  width: auto;
  margin-top: 5px;
}

.manager-delete-button,
.manager-add-button {
  padding: 6px 10px;
  border: none;
  border-radius: 4px;
  background-color: #dc3545; /* rouge pour delete */
  color: white;
  cursor: pointer;
}

.manager-add-button {
  background-color: #28a745; /* vert pour add */
}

.manager-delete-button:hover {
  background-color: #a71d2a;
}

.manager-add-button:hover {
  background-color: #1e7e34;
}

/* Team Members section */
.team-members-section {
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 5px;
  background-color: #fff;
  display: inline-flex;
  flex-direction: column;
  gap: 20px;
  max-width: fit-content;
}

.team-members-section h2 {
  margin-top: 0;
}

.team-members-content {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
  width: auto;
}

.team-members-list table {
  width: auto;
  margin: 0;
  table-layout: auto;
  border-collapse: collapse;
  white-space: nowrap;
}

.team-members-list th,
.team-members-list td {
  border: 1px solid #ddd;
  padding: 8px 12px;
  text-align: left;
}

.team-members-add {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.team-members-add input,
.team-members-add select {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.team-members-add button {
  @extend .common-add button;
}

