.subject-types-content {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
  width: auto;
}

.subject-types-list table {
  width: auto;
  margin: 0;
  table-layout: auto;
  border-collapse: collapse;
