}

.subject-types-add {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.subject-types-add input {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.subject-types-add button {
  @extend .common-add button;
}

/* Tab system */
.admin-tabs {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.admin-tabs button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px 4px 0 0;
  background-color: #f1f1f1;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s;
}

.admin-tabs button.active {
  background-color: #007bff;
  color: white;
}

.admin-tabs button:hover:not(.active) {
  background-color: #ddd;
}

.admin-content {
  border: 1px solid #ddd;
  border-top: none;
  border-radius: 0 0 5px 5px;
  padding: 20px;
  background-color: #fff;
}
